<template>
  <div class="main-container">
    <b-navbar toggleable="lg" type="dark" variant="dark" fixed="ture">
    <b-navbar-brand><router-link to="/home" tag="span">移动应用鉴别系统</router-link></b-navbar-brand>

    <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <b-navbar-nav>
        <router-link to="/home" tag="span" exact><b-nav-item>主页</b-nav-item></router-link>
        <router-link to="/document"  tag="span" exact> <b-nav-item >文档</b-nav-item></router-link>
      </b-navbar-nav>

      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown right v-if="isLogined">
          <template slot="button-content"><em>{{username}}</em></template>
          <b-dropdown-item><router-link :to="{path:'/person',query:{username:username}}" tag="span" exact>用户中心</router-link></b-dropdown-item>
          <b-dropdown-item @click="logout"><span>登出</span></b-dropdown-item>
        </b-nav-item-dropdown>
        <b-nav-item-dropdown right v-else>
          <template slot="button-content"><span>登录</span></template>
         <b-dropdown-item> <router-link :to="{path:'/login'}" tag="span" exact>已有账户</router-link></b-dropdown-item>
          <b-dropdown-item><router-link :to="{path:'/registe'}" tag="span" exact>注册</router-link></b-dropdown-item>
        </b-nav-item-dropdown>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
    <router-view></router-view>
    <footer></footer>
  </div>
</template>

<script>
import userApi from '@/Api/user-function'
export default {
  name: 'Main',
  data () {
    return {
      isLogined: false,
      username: 'admin'
    }
  },
  methods: {
    logout () {
      let formData = new FormData()
      formData.append('session', window.sessionStorage.getItem('session'))
      userApi.logout(formData).then(response => {
        this.$message.success('登出成功')
        window.sessionStorage.clear()
        this.isLogined = false
      }).catch(error => {
        this.$message.error(error.msg || '登出失败')
      })
    }
  },
  created () {
    if (window.sessionStorage.getItem('username')) {
      this.username = window.sessionStorage.getItem('username')
      this.isLogined = true
    }
  }
}
</script>

<style scope>
  .main-container{}
</style>



