<template>
  <div class="set">
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-md-3 push-md-9 col-xl-3 push-xl-9">
          <div class="card">
            <div class="card-header" align="center">
              <img
                src="http://blog.booleanln.cn/img/favicon.jpg"
                class="avatar img-circle img-responsive"
              >
              <p>
                <strong>{{username}}</strong>
              </p>
            </div>
            <div class="card-block">
              <ul class="list-group">
                <li class="list-group-item">
                  <el-button
                    class="font-weight-bold text-success btn btn-default"
                  >历史记录</el-button>
                </li>
                <li class="list-group-item">
                  <el-button
                    class="font-weight-bold text-danger btn btn-default"
                    @click="logout"
                  >登出</el-button>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="col-xs-12 col-md-9 pull-md-3 col-xl-9 pull-xl-3">
          <div class="list-group history">
            <a href="#" class="list-group-item list-group-item-action active">历史记录</a>
            <div v-for="(item,index) in lists" :key="index">
              <div class="list-group-item">
                <a
                  :href="item.name"
                  class="d-flex justify-content-between align-items-center"
                >{{item.name}}</a>
                <span class="badge badge-primary badge-pill">{{item.checktime}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import userApi from '@/Api/user-function'
export default {
  data () {
    return {
      username: 'admin',
      lists: []
    }
  },
  created () {
    this.getHistory()
  },
  methods: {
    logout () {
      let formData = new FormData()
      formData.append('session', window.sessionStorage.getItem('session'))
      userApi.logout(formData).then(response => {
        this.$message.success('登出成功')
        window.sessionStorage.clear()
      }).catch(error => {
        this.$message.error(error.msg || '登出失败')
      })
    },
    getHistory () {
      let formData = new FormData()
      let username = window.sessionStorage.getItem('username')
      formData.append('username', username)
      userApi.getHistory(formData).then(response => {
        this.$message.success('获取成功')
        this.lists = response.data
      }).catch(error => {
        this.$message.error(error.msg || '获取失败')
      })
    }
  }
}
</script>

<style scope>
 .card{
    margin-top: 50px;
  }
  .avatar {
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 60px;
  }

  .mpavatar {
    height: 30px;
    margin: 0 auto;
    margin-top: 2px;
    margin-bottom: 2px;
  }

  .img-circle {
    border-radius: 50%;
  }

  .sidebar-remove {
    margin-top: 8px;
  }
  .history {
    margin-top: 80px;
    margin-left: 20px;
  }
</style>



