<template>
  <div class="box">
    <div class="topImag">
      <div class="but">
        <router-link to="/api"><button class="btn  btn-outline-primary btn-lg" style="margin: 20px">接口文档</button></router-link>
        <button class="btn  btn-outline-info btn-lg" style="margin: 20px" @click="scrollToResult">接口测试</button>
      </div>
      <div class="title">
        <p>文字甄别接口</p>
      </div>
      <div class="topZ"></div>
    </div>
    <div class="function">
      <div class="intro">
        <h1 class="text-center">功能介绍</h1>
        <p class="lead">文字甄别接口，采用向量机对字符串继续分割、校验，甄别出具有黄赌毒倾向的词语</p>
        <div class="ins">
          <img src="../assets/images/intro-bg.png" alt="" class="img-fluid" style="max-width:70%">
        </div>
      </div>
      <div class="test">
        <h1 class="text-center">接口测试</h1>
        <p class="lead">接口测试，输入字符串，对该段文字进行甄别</p>
        <div class="getStr">
          <div class="input-group mb-3">
            <input type="text" class="form-control" placeholder="输入测试字符串" aria-label="测试文本输入框" v-model="text">
            <div class="input-group-append" >
              <button class="btn btn-outline-info" type="button" @click="identify">甄别</button>
            </div>
          </div>
          <div class="RecoRes">
            <p class="text-success text-center">{{result}}</p>
            <ol v-for="(item,index) in words" class="text-center" :key="index">
              <li class="text-left text-success ">违规词语：{{item}}</li>
            </ol>
          </div>
        </div>
      </div>
      <div class="test">
        <h1 class="text-center">文本上传测试</h1>
        <p class="lead">上传要测试的文本，格式支持：.txt等</p>
        <div class="getStr">
          <button href="/recognize/text"  class="file">
          <input type="file" name="txt" id="txt" accept=".txt" @input="identifyTxt($event)" multiple>文本识别
          </button>
          <div class="progress" style="border:1px solid black;width:50%;margin: 10px auto; ">
            <div class="progress-bar progress-bar-striped  progress-bar-animated" role="progressbar" :aria-valuenow="progress" aria-valuemin="0" aria-valuemax="100"  v-bind:style="{width:progress+'%'}">
              <span class="sr-only">{{progress}}%完成</span>
            </div>
          </div>
          <div class="RecoRes">
            <ol v-for="(item,index) in results" class="text-center" style="margin: 20px auto;width: 70%" :key="index">
              <li class="text-info text-left">{{item.result}}</li>
              <ol v-for="(word,index) in item.words" class="text-center" :key="index">
                <li class="text-left text-success ">违规词语：{{word}}</li>
              </ol>
            </ol>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import txtApi from '@/Api/restful-api'
import textApi from '@/Api/user-function'
export default {
  data () {
    return {
      message: '',
      result: '',
      results: [],
      progress: 0,
      words: [],
      text: ''
    }
  },
  methods: {
    setProgress () {},
    identify () {
      let formData = new FormData()
      formData.append('text', this.text)
      textApi.textIdentify(formData).then(response => {
        this.result = response.data[0].result
        this.words = response.data[0].words
        this.$message.success('甄别成功')
      }).catch(error => {
        this.$message.error(error.msg || '甄别失败')
      })
    },
    identifyTxt (e) {
      let formData = new FormData()
      for (var i = 0; i < e.target.files.length; i++) {
        formData.append('txt', e.target.files[i])
      }
      this.progress = 50
      txtApi.txtIdentify(formData).then(response => {
        this.$message.success('甄别成功')
        setTimeout(() => { this.progress = 100 }, 100)
        this.results = response.data
      }).catch(error => {
        this.$message.error(error.msg || '甄别失败')
      }).finally(() => {
        setTimeout(() => { this.progress = 0 }, 1000)
      })
      // 一个文件对象都需要append下，直接append获取的只是list
    },
    // 滑动到测试区
    scrollToResult () {
      let height = document.body.offsetHeight
      let toTop = height * 0.28
      window.scrollTo({
        top: toTop,
        behavior: 'smooth'
      })
    }
  }
}
</script>

<style scope>
 .box{
      text-align: center;
      font-family: arial,tahoma,Hiragino Sans GB,Microsoft YaHei,sans-serif;
      position:relative;
  }
  .topImag{
    height: 450px;
    background: url("../assets/images/banner.jpg");
    background-size: cover;
    text-align: inherit;
  }
  .but{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    padding-top: 200px;
    height: 450px;
  }
  .topZ{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,.3);
    height: 450px;
  }
  .title{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding-top: 120px;
    color: white;
    /*font-weight: bold;*/
    font-family: "Adobe 黑体 Std R";
    z-index: 1;
    letter-spacing: 10px;
    font-size: 50px;
    height: 450px;
  }
  .function{
    padding: 20px;
    background-color: #eee;
    color: #333;
  }
  .ins{
    margin-top: 40px;
  }
  .test{
    margin: 40px;
  }
  .getStr{
    width: 50%;
    margin: 40px auto;
  }
  .RecoRes{

  }
  .file{
    margin-top:0px;
    position: relative;
    display: inline-block;
    background: transparent;
    border: 1px solid #000;
    border-radius: 4px;
    padding: 4px 12px;
    overflow: hidden;
    color: #333;
    text-decoration: none;
    text-indent: 0;
    line-height: 65px;
    height: 70px;
    width: 200px;
    font-size: 20px;
    margin-right: 20px;
  }
  .file input{
    position: absolute;
    font-size: 100px;
    right: 0;
    top: 0;
    opacity: 0;
  }
  .file:hover{
    background-color: lightyellow;
  }
</style>



