<template>
 <div class="login-container">
    <div class="box-wrapper">
      <div class="registe-box" v-if="isRegiste">
        <div class="registe">
          <el-input v-model="userConfig.username" placeholder="输入账号名"></el-input>
          <el-input v-model="userConfig.password" placeholder="输入密码" show-password></el-input>
          <el-input v-model="passwordAgain" placeholder="请再次输入密码" show-password></el-input>
          <el-input v-model="userConfig.sex" placeholder="性别"></el-input>
          <el-input v-model="userConfig.email" placeholder="请输入电子邮箱"></el-input>
          <el-button type="primary" style="margin-top:10px;" @click="registe">注册</el-button>
        </div>
      </div>
      <div class="login-box" v-else>
        <div class="login">
          <el-input v-model="userConfig.username" placeholder="输入账号"></el-input>
          <el-input v-model="userConfig.password" placeholder="输入密码" show-password></el-input>
          <el-button type="primary" style="margin-top:10px;" @click="login">登录</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import userApi from '@/Api/user-function'
export default {
  data () {
    return {
      userConfig: {
        username: '',
        password: '',
        sex: '',
        email: ''
      },
      passwordAgain: '',
      isRegiste: true
    }
  },
  created () {
    this.isRegiste = this.$route.path.match('registe') !== null
  },
  methods: {
    login () {
      let formData = new FormData()
      formData.append('username', this.userConfig.username)
      formData.append('password', this.userConfig.password)
      if (!this.userConfig.username || !this.userConfig.password) {
        this.$message.error('请输入用户名及密码')
        return
      }
      userApi.login(formData).then(response => {
        this.$message.success('登录成功')
        window.sessionStorage.setItem('session', response.data.session)
        window.sessionStorage.setItem('username', this.userConfig.username)
        this.$router.push({
          name: 'main',
          query: {
            username: this.userConfig.username
          }
        })
      }).catch(error => {
        this.$message.error(error.msg || '登录失败')
      })
    },
    registe () {
      this.$message.success('注册成功')
    }
  }
}
</script>

<style lang="scss" scope>
.login-container {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #545c64;
  .box-wrapper {
    width: 100%;
    position: relative;
    height: 300px;
    .login-box {
      width: 500px;
      height: 350px;
      background: #fff;
      // margin: auto;
      position: absolute;
      top: 40%;
      left: 35%;
      border-radius: 10px;
      .login {
        margin: 70px;
        .el-input {
          margin-top: 20px;
        }
      }
    }
    .registe-box{
      width: 500px;
      height: 500px;
      background: #fff;
      // margin: auto;
      position: absolute;
      top: 40%;
      left: 35%;
      border-radius: 10px;
      .registe{
        margin: 70px;
        .el-input {
          margin-top: 20px;
        }
      }
    }
  }
}
</style>



