<template>
  <div class="api container">
    <div class="text-title">
      <h2 class="text-center">{{content.title}}</h2>
    </div>
    <div class="dropdown-divider"></div>
    <div class="text-body">
      <h3>接口设计</h3>
      <img :src="imgUrl" alt="" v-for="(imgUrl,index) in content.imgs" :key="index">
      <div style="font-size: 20px">
      <p><strong>URL：</strong><em>{{content.url}}</em></p>
      <p><strong>参数：{{content.params}}</strong></p>
      <ul>
        <li>content-type:form-data</li>
        <li>Image:imagefile</li>
      </ul>
      <p><strong>返回值：识别结果</strong></p>
      <ol>
        <li v-for="(param,index) in content.returnParams" :key="index">{{param}}</li>
      </ol></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Document',
  props: {
    content: {
      type: Object,
      default: function () {
        return {
          title: 'apk识别接口',
          url: 'http://54.245.203.1:5000/predict',
          params: '识别图像，可多选，批量上传',
          returnParams: ['code', 'data', 'msg'],
          imgs: [
            require('@/assets/images/image.png'),
            require('@/assets/images/image2.png'),
            require('@/assets/images/image3.png')
          ]
        }
      }
    }
  },
  mounted () {
    console.log(this.content)
  },
  data () {
    return {}
  }
}
</script>

<style scope>
.api{
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #eee;
  }
  .text-body{
    padding: 20px;

  }
</style>



