<template>
  <div class="set">
    <div class="box">
      <div class="row">
        <div class="col-xs-12  col-md-3 push-md-9 col-xl-3 push-xl-9" >
          <div class="card">
            <div class="card-block">
              <ul class="list-group">
                <li class="list-group-item"><el-button class="text-success btn btn-default" @click="content = contents[0]">Apk接口</el-button></li>
                <li class="list-group-item"><el-button class="text-warn btn  btn-default" @click=" content = contents[1]"> 图像识别接口</el-button></li>
                <li class="list-group-item"><el-button class="text-danger btn  btn-default" @click="content = contents[2]">文本甄别接口</el-button></li>
              </ul>
            </div>
          </div>
        </div>
        <div class="col-xs-12 col-md-9 pull-md-3 col-xl-9 pull-xl-3">
          <documentPage :content="content"></documentPage>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import documentPage from '@/pages/document-page'
export default {
  components: {
    documentPage
  },
  data () {
    return {
      contents: [
        {
          title: 'apk识别接口',
          url: 'http://www.booleanln.cn/api/getRes',
          params: '识别图像，可多选，批量上传',
          returnParams: ['code', 'data', 'msg'],
          imgs: [
            require('@/assets/images/image.png'),
            require('@/assets/images/image2.png'),
            require('@/assets/images/image3.png')
          ]
        },
        {
          title: '图像文字识别接口',
          url: 'http://54.245.203.1:5000/predict',
          params: '识别图像，可多选，批量上传',
          returnParams: ['code', 'data', 'msg'],
          imgs: [
            require('@/assets/images/image.png'),
            require('@/assets/images/image2.png'),
            require('@/assets/images/image3.png')
          ]
        },
        {
          title: '文字甄别接口',
          url: 'http://54.190.58.40:5000/filter',
          params: '识别文字或文本，可多选，批量上传',
          returnParams: ['code', 'data', 'msg'],
          imgs: [
            require('@/assets/images/image.png'),
            require('@/assets/images/image2.png'),
            require('@/assets/images/image3.png')
          ]
        }
      ],
      content: {
        title: 'apk识别接口',
        url: 'http://www.booleanln.cn/api/getRes',
        params: '识别图像，可多选，批量上传',
        returnParams: ['code', 'data', 'msg'],
        imgs: [
          require('@/assets/images/image.png'),
          require('@/assets/images/image2.png'),
          require('@/assets/images/image3.png')
        ]
      }
    }
  },
  methods: {}
}
</script>

<style scope>
 .box{
    width: 80%;
    margin: 20px auto;
  }
  .card{
    margin-top: 20px;
    font-size:xx-large;
  }
  .avatar {
    height: 75px;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .mpavatar {
    height: 30px;
    margin: 0 auto;
    margin-top: 2px;
    margin-bottom: 2px;
  }

  .img-circle {
    border-radius: 50%;
  }

  .sidebar-remove {
    margin-top: 8px;
  }
</style>



