import { httpFileInstance } from '@/Api/httpInstance'
const restApi = {
  getImgText: function (params) {
    return httpFileInstance.post('/api/image', params)
  },
  txtIdentify: function (params) {
    return httpFileInstance.post('/api/txtReco', params)
  },
  apkSourceGet: function (params) {
    return httpFileInstance.post('/api/apkResources', params)
  },
  apkIdentify: function (params) {
    return httpFileInstance.post('/api/apk', params)
  },
  apkIdentifyUser: function (params) {
    return httpFileInstance.post('/api/apk/' + window.sessionStorage.getItem('username'), params)
  }
}
export default restApi



