import { httpInstance } from '@/Api/httpInstance'

const Api = {
  login: function (params) {
    return httpInstance.post('/auth/login', params)
  },
  registe: function (params) {
    return httpInstance.post('/auth/register', params)
  },
  logout: function (params) {
    return httpInstance.post('/auth/logout', params)
  },
  getHistory: function (params) {
    return httpInstance.post('/auth/history', params)
  },
  insertHistory: function (params) {
    return httpInstance.post('/auth/insertHistory', params)
  },
  textIdentify: function (params) {
    return httpInstance.post('/api/textReco', params)
  }
}
export default Api



