<template>
  <div class="home-container">
    <div class="main">
      <div class="container main-box">
        <h1 class="name">APK在线检测</h1>
        <p>上传文件，一键测试！</p>
        <div>
          <button type="button" class="file" @click="dialogVisible = true">图像文本检测</button>
          <button type="button" class="file" @click="dialogApkVisible = true">APK鉴别</button>
          <a :href="downLoadUrl" class="client">下载客户端程序，模拟运行APK</a>
        </div>
        <el-dialog title="" :visible.sync="dialogVisible" :show-close="false" :center="true">
          <b-button  variant="outline-primary"  :block="true"> <router-link to="imageApi" tag="span">图片文字Api测试</router-link></b-button>
          <b-button variant="outline-success" :block="true"> <router-link to="textApi" tag="span">文本Api测试</router-link></b-button>
          <b-button variant="outline-danger"  :block="true"><router-link to="document" tag="span">文档查看</router-link></b-button>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialogApkVisible" :show-close="false" :center="true" class="apk-dialog">
          <b-button  variant="outline-primary" :block="true">
            <input type="file" class="-input-hid" name="apkfile" ref="apkInput" id="apk" accept=".apk,.zip"  v-on:input="apkSourceGet($event)" multiple="multiple">apk资源获取
          </b-button>
          <b-button variant="outline-success"  :block="true">
            <input type="file" class="-input2-hid" name="apkfile" ref="apkIdentifyInput" id="apk" accept=".apk,.zip"  v-on:input="apkIdentify($event)" multiple="multiple">apk一键鉴别
          </b-button>
          <b-button variant="outline-danger"  :block="true"><router-link to="document" tag="span">文档查看</router-link></b-button>
          <div class="progress" style="margin:5px auto">
            <div class="progress-bar progress-bar-striped  progress-bar-animated" role="progressbar" :aria-valuenow="progress" aria-valuemin="0" aria-valuemax="100"  :style="{width:progress+'%'}" >
              <span class="sr-only">{{progress}}%完成</span>
            </div>
          </div>
        </el-dialog>
      </div>
    </div>

    <div class="guide">
      <div class="container">
        <h2>用户指南</h2>
        <ul class="list-group">
          <li class="list-group-item">图片识别说明</li>
          <li class="list-group-item list-group-item-primary">选择图片文件</li>
          <li class="list-group-item list-group-item-secondary">上传图片，</li>
          <li class="list-group-item list-group-item-success">等待数秒，识别结果返回</li>
          <li class="list-group-item">压缩文件识别说明</li>
          <li class="list-group-item list-group-item-primary">选择图片压缩包</li>
          <li class="list-group-item list-group-item-secondary">上传至Flask服务器</li>
          <li class="list-group-item list-group-item-success">识别算法使用神经网络densenet,keras实现</li>
          <li class="list-group-item list-group-item-dark">返回结果csv文件</li>
          <li class="list-group-item">APK检测说明</li>
          <li class="list-group-item list-group-item-primary">选择APK文件</li>
          <li class="list-group-item list-group-item-secondary">上传至Flask服务器</li>
          <li class="list-group-item list-group-item-success">对APK进行解析模拟运行获取资源文件</li>
          <li class="list-group-item list-group-item-dark">对其进行图片文字识别返回检测报告</li>
        </ul>
      </div>
    </div>
    <div class="jumbotron" style="background-color: #f6f6f6">
      <h1 class="text-center res">结果展示</h1>
      <p class="lead text-center">识别结果如下，以列表形式展示</p>
      <div class="sbRes">
        <ol>
          <li v-for="(url,index) in urls" style="list-style: none" :key="index">
            <a :href="url">{{url}}</a>
          </li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script>
import apkApi from '@/Api/restful-api'
export default {
  name: 'dasdas',
  data () {
    return {
      urls: [],
      showModals: false,
      isModalVisible: false,
      secondModalVisible: false,
      downLoadUrl: 'http://www.booleanln.cn/downfile/Client.zip',
      dialogVisible: false,
      dialogApkVisible: false,
      progress: 0
    }
  },
  methods: {
    apkSourceGet (e) {
      this.progress = 50
      let fieldData = new FormData()
      let files = this.$refs.apkInput.files
      for (let i = 0; i < files.length; i++) {
        fieldData.append('apk', files[i])
      }
      apkApi.apkSourceGet(fieldData).then(response => {
        this.$message.success('获取成功')
        this.urls = response.data
        this.progress = 100
      }).catch(error => {
        this.$message.error(error.msg || '获取失败')
      }).finally(() => {
        setTimeout(() => {
          this.progress = 0
          this.dialogApkVisible = false
          window.scrollTo({
            top: document.body.offsetHeight * 2,
            behavior: 'smooth'
          })
        }, 1000)
      })
    },
    apkIdentify (e) {
      if (window.sessionStorage.getItem('username')) {
        this.apkIdentifyUser(e)
      } else {
        this.apkIdentifyNoUser(e)
      }
    },
    apkIdentifyUser (e) {
      this.progress = 50
      let fieldData = new FormData()
      let files = this.$refs.apkIdentifyInput.files
      for (let i = 0; i < files.length; i++) {
        fieldData.append('apk', files[i])
      }
      apkApi.apkIdentifyUser(fieldData).then(response => {
        this.$message.success('获取成功')
        this.urls = response.data
        this.progress = 100
        window.scrollTo({
          top: document.body.offsetHeight * 2,
          behavior: 'smooth'
        })
      }).catch(error => {
        this.$message.error(error.msg || '检测失败')
      }).finally(() => {
        setTimeout(() => {
          this.progress = 0
          this.dialogApkVisible = false
        }, 1000)
      })
    },
    apkIdentifyNoUser (e) {
      this.progress = 50
      let fieldData = new FormData()
      let files = this.$refs.apkIdentifyInput.files
      for (let i = 0; i < files.length; i++) {
        fieldData.append('apk', files[i])
      }
      apkApi.apkIdentify(fieldData).then(response => {
        this.$message.success('获取成功')
        this.urls = response.data
        this.progress = 100
        window.scrollTo({
          top: document.body.offsetHeight * 2,
          behavior: 'smooth'
        })
      }).catch(error => {
        this.$message.error(error.msg || '检测失败')
      }).finally(() => {
        setTimeout(() => {
          this.progress = 0
          this.dialogApkVisible = false
        }, 1000)
      })
    }
  }
}
</script>

<style lang="scss" scope>
  .home-container {
    .main {
      background: #ffffff no-repeat center center;
      background-color: #6993a8;
      background-image:url("./../assets/images/bg.jpg");
      background-position: 40% 30%;
      background-size: cover;
      color: #fff;
      position: relative;
      overflow: hidden;
      height: 900px;
      width: 100%;
      text-align: center;
      // 中间box
      .main-box{
        margin-top: 100px;
        // APK检测
        .name{
          font-size:58.5px;
          margin-right: 100px;
          &:before{
            background-image: url("./../assets/images/upload.png");
            content: '';
            display: inline-block;
            vertical-align: middle;
            width: 120px;
            height: 120px;
            margin-right: 15px;
            background-size: 100% 100%;
          }
        }
        // 文件上传按钮
        .file{
          margin-top: 50px;
          position: relative;
          display: inline-block;
          background: transparent;
          border: 1px solid #99D3F5;
          border-radius: 4px;
          padding: 4px 12px;
          overflow: hidden;
          color: #ffffff;
          text-decoration: none;
          text-indent: 0;
          line-height: 65px;
          height: 70px;
          width: 200px;
          font-size: 20px;
          margin-right: 20px;
          &:hover{
          background: #AADFFD;
          border-color: #78C3F3;
          color: #ffffff;
          text-decoration: none;
        }
        // 输入框
        input{
          position: absolute;
          font-size: 100px;
          right: 0;
          top: 0;
          opacity: 0;
          }
        }
        a{
          display: block;
        }
        .apk-dialog{
          .-input-hid{
            position: absolute;
            font-size: 50px;
            right: 0;
            top: 0;
            opacity: 0;
          }
          .-input2-hid{
            position: absolute;
            font-size: 50px;
            right: 0;
            top: 80px;
            opacity: 0;
          }
        }
      }
      p{
          text-align: center;
          font-size: 26px;
          line-height: 30px;
          font-weight:normal;
      }
      .btn-modal{
        margin: 20px auto;
      }
    }
    // 引导文档
    .guide{
      padding-bottom: 180px;
      border-bottom: solid 1px #ebebeb;
      background: #ffffff;
      h2{
        color: #333;
        position: relative;
        text-align: center;
        padding-top: 90px;
        font-size: 60px;
        &:before{
          display: block;
          content: '';
          height:140px;
          top: 0;
          left: 0;
          margin: auto;
          right: 0;
          background: url('./../assets/images/guide.png') no-repeat center center;
        }
    }
    }
    // 结果区
    .sbRes{
      width: 50%;
      margin: 20px auto;
      font-size: 20px;
    }
  }
</style>



