import Main from '@/components/Main'
import Home from '@/pages/home'
import Document from '@/pages/document'
import Login from '@/components/Login'
import UserHome from '@/pages/user-home'
import ImageReconize from '@/pages/img-recognize'
import TextReconize from '@/pages/text-recognize'
const mainRoutes = [
  {
    name: 'home',
    path: '/home',
    component: Home
  },
  {
    name: 'document',
    path: '/document',
    component: Document
  },
  {
    name: 'userHome',
    path: '/person',
    component: UserHome
  },
  {
    name: 'imageApi',
    path: 'imageapi',
    component: ImageReconize
  },
  {
    name: 'textApi',
    path: 'textapi',
    component: TextReconize
  }
]
const routes = [
  {
    name: 'main',
    path: '/',
    component: Main,
    redirect: '/home',
    children: mainRoutes
  },
  {
    name: 'login',
    path: '/login',
    component: Login
  },
  {
    name: 'registe',
    path: '/registe',
    component: Login
  }
]
export default routes



