<template>
<div class="box">
    <div class="topImag">
      <div class="but">
        <router-link to="/document"><button class="btn btn-outline-primary btn-lg" style="margin: 20px">接口文档</button></router-link>
        <button class="btn  btn-outline-info btn-lg" style="margin: 20px" @click="scrollToResult">接口测试</button>
      </div>
      <div class="title">
        <p>图片文字识别</p>
      </div>
      <div class="topZ"></div>
    </div>
    <div class="container" style="margin: 0 auto;width: 50%;padding: 70px 100px 200px 100px">
      <div class="test-img">
        <h1 class="text-center">接口测试</h1>
        <p class="lead">选择本地图片进行上传测试,可通过 <span class="text-info">ctrl</span> 键点选多张图片</p>
      </div>
      <div class="img-uploader"
           @drop.stop="handleDrop"
           ref="uploader">

        <!--没有图片显示点击上传-->
        <p class="img-uploader-placeholder" v-if="!hasImages">{{placeholder}}</p>

        <!--图片预览列表-->
        <div v-if="hasImages" class="img-uploader-preview-list">
          <div v-for="(data,index) in imageDataList" class="img-uploader-preview" :key="index">

            <div class="preview-img">
              <img :src="data"/>
            </div>
            <!--信息窗-->
            <div class="img-uploader-mask" v-if="hasImages">
              <p class="img-uploader-file-name" @click='openInput()'>{{fileNameList[index]}}</p>
            </div>
            <img src="../assets/images/delete.png" class="img-uploader-delete-btn" @click="deleteImg(index)"/>
          </div>
        </div>
        <label for="imgInput" class="img-uploader-label" v-if="!hasImages"></label>
        <!-- input-->
        <input
          style="display: none"
          id="imgInput"
          ref="imgInput"
          type="file"
          accept="image/gif,image/jpeg,image/jpg,image/png,image/svg"
          multiple="multiple"
          @change="handleFileChange"/>

        <!--错误提示-->
        <div class="img-uploader-error" v-if="errorText.length">{{errorText}}</div>

        <!--文件数-->
        <div class="img-uploader-count" v-if="countText.length">{{countText}}</div>
        <div class="upload-btn">
          <button class="btn btn-primary btn-lg btn-block" @click="imgIdentify">文字检测</button>
        </div>
      </div>
      <div class="progress">
        <div class="progress-bar progress-bar-striped  progress-bar-animated" role="progressbar" :aria-valuenow="progress" aria-valuemin="0" aria-valuemax="100"  v-bind:style="{width:progress+'%'}">
            <span class="sr-only">{{progress}}%完成</span>
        </div>
      </div>
    </div>
    <div class="">
      <div class="jumbotron" style="background-color: #f6f6f6">
        <h1 class="text-center res">结果展示</h1>
        <p class="lead">识别结果如下，以列表形式展示，一行代表一张图片的识别结果</p>
        <div class="sbRes">
          <ol v-for="(item,index) in read_res" class="text-center" :key="index">
            <li class="text-left text-info"><mark>{{item}}</mark></li>
          </ol>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import resizeImage from '@/Api/resize'
import imgApi from '@/Api/restful-api'
export default {
  data () {
    return {
      // input 的id
      // inputId: '',
      // 预览图片地址
      imageDataList: [],
      // 文件名
      fileNameList: [],
      // 图片计数
      countText: '',
      read_res: [],
      progress: 0,
      flag: 0,
      placeholder: '点击上传图片',
      errorText: '',
      maxSize: 999998
    }
  },
  computed: {
    // 是否有图片
    hasImages () {
      return this.imageDataList.length > 0
    },
    // 格式化的文件大小，可读的
    sizeFormatted () {
      let result = 0
      if (this.maxSize < 1024) {
        result = this.maxSize + 'K'
      } else {
        result = (this.maxSize / 1024).toFixed(this.maxSize % 1024 > 0 ? 2 : 0) + 'M'
      }
      return result
    }
  },
  mounted () {
    // this.inputId = this.id || Math.round(Math.random() * 100000)
    // console.log(this.inputId)
    // let arr = ['drop', 'dragenter', 'dragover', 'dragleave']
    // arr.forEach((eventName) => {
    //   this.preventDefaultEvent(eventName)
    // })
  },
  methods: {
    handleFileChange () {
      let input = this.$refs.imgInput
      let files = input.files
      this.handleFile(files)
    },
    handleDrop (e) {
      let files = e.dataTransfer.files
      this.handleFile(files)
    },
    preventDefaultEvent (eventName) {
      document.addEventListener(eventName, function (e) {
        e.preventDefault()
      }, false)
    },
    openInput () {
      document.getElementById('imgInput').click()
    },
    deleteImg (index) {
      if (this.flag === 1) {
        this.$message.error('图片识别中，无法操作')
        return
      }
      this.imageDataList.splice(index, 1)
      this.countText = `${this.imageDataList.length}张图片`
      this.progress = 0
    },
    handleFile (files) {
      if (files && files.length > 0) {
        this.fileNameList.length = 0
        this.imageDataList.length = 0
      }
      for (let i = 0; i < files.length; i++) {
        let file = files[i]
        let size = Math.floor(file.size / 1024)
        if (size > this.maxSize) {
          this.errorText = `文件大小不能超过${this.sizeFormatted}`
          return false
        }
        this.fileNameList.push(file.name)
      }
      if (files && files.length > 0) {
        this.countText = `${files.length}张图片`
      }
      this.preview(files)
    },
    preview (files) {
      let _this = this
      if (!files || !window.FileReader) return

      for (let i = 0; i < files.length; i++) {
        let file = files[i]
        let reader = new FileReader()
        reader.onload = function (e) {
          resizeImage(e.target.result, 150, 150, function (result) {
            _this.imageDataList.push(result)
          })
        }
        reader.readAsDataURL(file)
      }
    },
    imgIdentify () {
      let files = this.$refs.imgInput.files
      let fieldData = new FormData()
      this.read_res = []
      this.flag = 1
      if (files.length === 0) return
      for (let i = 0; i < files.length; i++) {
        fieldData.append('img', files[i])
      }
      this.progress = 50
      imgApi.getImgText(fieldData).then(response => {
        this.progress = 100
        this.$message.success('识别成功')
        this.read_res = response.data
        this.scrollToResult()
      }).finally(() => {
        setTimeout(() => {
          this.progress = 0
          this.flag = 0
        }, 1000)
      }).catch(error => {
        this.$message.error(error.msg || '识别失败')
      })
    },
    // 滑动到测试区
    scrollToResult () {
      let height = document.body.offsetHeight
      let toTop = height * 0.28
      window.scrollTo({
        top: toTop,
        behavior: 'smooth'
      })
    }
  }
}
</script>

<style scope>
.box{
    text-align: center;
    font-family: arial,tahoma,Hiragino Sans GB,Microsoft YaHei,sans-serif;
    position:relative;
  }
  .topImag{
    height: 450px;
    background: url("../assets/images/banner.jpg");
    background-size: cover;
    text-align: inherit;
  }
  .but{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    padding-top: 200px;
    height: 450px;
  }
  .topZ{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0,0,0,.3);
    height: 450px;
  }
  .title{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding-top: 120px;
    color: white;
    font-family: "Adobe 黑体 Std R";
    z-index: 1;
    letter-spacing: 10px;
    font-size: 50px;
    height: 450px;
  }
  .img-uploader {
    position: relative;
    display: inline-block;
    width: 100%;
    height: calc(200px + 25px * 2);
    border-radius: 5px;
    background: #ebebeb;
    margin: 0 auto;
  }
  .img-uploader-placeholder {
    margin: 0;
    position: absolute;
    font-size: 30px;
    width: 100%;
    color: #aaa;
    text-align: center;
    top: 50%;
    transform: translate(0%, -50%);
  }

  .img-uploader-preview-list {
    padding-top: 20px;
    padding-bottom: 20px;
    margin: 5px 10px;
    height: calc(200px + 18px * 2);
    /*width: 100%;*/
    white-space: nowrap;
    overflow: hidden;
    overflow-x: auto;
    -webkit-backface-visibility: hidden;
    -webkit-perspective: 1000;
    -webkit-overflow-scrolling: touch;
    text-align: center;
  }

  .img-uploader-preview {
    display: inline-block;
    z-index: 2;
    min-height: 200px;
    margin: 10px;
    border-radius: 10px;
    background: #333;
    transition: 0.3s cubic-bezier(0.3, 0, 0.2, 1);
  }

  .img-uploader-preview:hover {
    transform: scale(1.02);
  }

  .img-uploader-preview:hover .img-uploader-mask {
    display: block;
  }

  .img-uploader-preview:hover .img-uploader-delete-btn {
    display: block;
  }

  .img-uploader-preview .preview-img {
    width: 150px;
    height: 150px;
    overflow: hidden;
  }

  .img-uploader-label {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    cursor: pointer;
    margin-bottom: 0;
  }

  .img-uploader-mask {
    display: none;
    position: absolute;
    bottom: 0;
    width: 150px;
    border-radius: 1px;
    text-align: center;
    background: rgba(0, 0, 0, 0.6);
  }

  .img-uploader-delete-btn {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    margin: 5px;
    width: 25px;
    height: 25px;
  }

  .img-uploader-file-name {
    color: white;
    font-size: 5px;
    padding-top: 10px;
    margin: 0;
    display: inline-block;
    max-width: 90%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    cursor: pointer;
  }

  .img-uploader-error {
    color: #e55;
    font-size: 12px;
    position: absolute;
    bottom: -28px;
    width: 100%;
  }

  .img-uploader-count {
    color: #28a745;
    font-size: 16px;
    font-family: "Microsoft YaHei UI";
    font-weight: bold;
    position: absolute;
    bottom: -50px;
    width: 100%;
  }
  .upload-btn{
    position: absolute;
    bottom: -120px;
    width: 100%;
  }
  .result{
    margin: 10%;
  }
  .sbRes{
    width: 50%;
    margin: 20px auto;
    font-size: 20px;
  }
  .sbRes li{
    padding: 10px;
  }
</style>



